@import "./../common/styles/sass/mixins";

.mainBlock {
  background-color: rgba(232, 235, 238, 0.5);
  opacity: 1;
  min-height: 100vh;
  display: flex;
  align-items: center;
  scroll-behavior: smooth;

  @media (max-width: 820px) {
    padding: 180px 0 0 0;
  }
  @media (max-width: 800px) {
    padding: 120px 0 0 0;
  }
}

.mainContainer {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0;
  @include container();

  @media (max-width: 1099px) {
    flex-direction: column;
  }
  @media (max-width: 581px) {
    flex-direction: row;
  }

.textBlock {
  width: 500px;



  .greeting {
    font-size: 28px;
    margin: 0 0 16px;
    text-align: center;
    @media (max-width: 353px) {
      font-size: 24px;
      text-align: center;
    }
  }

  .title {
    margin: 0;
    font-size: 48px;
    text-align: center;
    @media (max-width: 602px) {
      text-align: center;
    }
    @media (max-width: 353px) {
      font-size: 36px;

    }
  }

  .skills {
    font-size: 28px;
    display: block;
    margin: 0 0 16px;
    text-align: center;
    @media (max-width: 353px) {
      font-size: 24px;
    }
  }

  .based {
    font-size: 18px;
    text-align: center;
    margin: 0 0 24px;
    @media (max-width: 602px) {
      text-align: center;
    }
  }


  .button {
    margin: 40px auto;
    display: block;
    background-color: transparent;
    border: none;
    outline: none;
    font-family: inherit;

  }

}

.photo {
  border-radius: 50%;
  margin-bottom: 20px;
  width: 400px;
  height: 400px;
  background: gray;
  margin-left: 35px;
  @media (max-width: 1099px) {
    margin-left: 0;
  }
  @media (max-width: 490px) {
    width: 270px;
    height: 270px;
  }
  @media (max-width: 350px) {
    width: 240px;
    height: 240px;
  }

  .image {
    display: block;
    width: 100%;

  }
}



}