@import "./../../common/styles/sass/variables";

.nav {
  display: flex;
  justify-content: space-between;
  min-width: 220px;
  flex-wrap: nowrap;
  scroll-behavior: smooth;
  @media (max-width: 785px)  {
    min-width: 0;
  }

  .item {
    list-style: none;
    margin-left: 2px;
    margin-right: 2px;
    @media (max-width: 785px) {
      display: none;
    }
    .active {
      color: #20c997;
    }


  }

  .link {
    color: $textColor;
    display: flex;
    align-items: center;
    height: 70px;
    padding: 0 13.6px;
    text-decoration: none;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    cursor: pointer;

  }

}
